  .header__menu {
    display: none;
  }
}
.header__link {
  margin-right: 50px;
}
.header__link a {
  transition: linear 250ms;
}
.header__link:last-child {
  margin-right: unset;
}
@media (max-width: 992px) {
  .header__link {
    margin-right: 25px;
  }
