  position: relative;
  height: 100vh;
  background-image: url(../../images/main-hero.png);
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 100px 0;
}
@media (max-width: 992px) {
  .main-hero__wrapper {
    background-position-y: 260px;
  }
}
