.grid__content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}
@media (max-width: 1200px) {
  .grid__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 992px) {
  .grid__content {
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }
}
.grid__content a.grid-content__item {
  display: block;
  overflow: hidden;
  border-radius: 15px;
  background-color: #fff;
  position: relative;
}

.grid-content .content {
  padding-top: 22px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.grid-content__item {
  position: relative;
}
.grid-content__item .circle,
.grid-content__item .fill {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #DB3AB4;
  z-index: 0;
}
.grid-content__item .circle {
  border-radius: 50%;
  transform: translateY(100%);
  transition: linear 450ms;
}
.grid-content__item .fill {
  transform: translateY(-100%);
  transition: linear 450ms;
}
.grid-content__item:hover .circle {
  transform: translateY(-50%);
}
.grid-content__item:hover .fill {
  transform: translateY(0%);
  transition-delay: 450ms;
}
.grid-content__item:hover h4.grid-content__title {
  color: #fff;
  transition: 450ms;
}
.grid-content__title, .grid-content__image {
  position: relative;
  z-index: 1;
}
.grid-content__image {
  display: flex;
  width: fit-content;
  margin: 0 auto;
}
.grid-content__image img {
  width: 100%;
}
