  cursor: pointer;
}
.grid-content__text.text-list li p {
  text-transform: none;
}
.grid-content__text.text-list li p:hover {
  transition: linear 250ms;
  color: #DB3AB4;
}
.grid-content__text.text-list li p:hover::before {
  transition: linear 250ms;
  background-color: #DB3AB4;
}
.grid-content__text.text-list p {
  position: relative;
  padding-left: 55px;
}
.grid-content__text.text-list p::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  display: block;
  width: 35px;
  height: 1px;
  background-color: #fff;
}
.grid-content.frames h4 {
  color: #000;
  text-align: center;
}
